.hero {
  height: 442px;
  margin-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  // background-color: $color-bcg-pink;

  @include for-size(mobile) {
    background-image: url(../images/phone/slider-pic7.png);
    background-size: 171px 410px;
    background-position: 117px 32px;
    background-repeat: no-repeat;
  }
  @include for-size(tablet) {
    margin-top: 0;
    height: 399px;
    min-width: 566px;
    background-image: url(../images/tablet/slider-pic7.png);
    background-size: 143px 336px;
    background-position: 378px 63px;
    padding-left: 34px;
    padding-right: 34px;
  }

  @include for-size(desktop) {
    background-image: url(../images/desktop/slider-pic7.png),
      url(../images/desktop/ellipse-hero-desk.png);
  }
  &__title {
    margin-bottom: 20px;
    padding-top: 133px;
    font-family: 'Titan One';
    font-size: 26px;
    line-height: 1.3;
    letter-spacing: 0.06em;
    color: $color-white-text;
    text-transform: uppercase;
    @include for-size(tablet) {
      font-size: 22px;
      font-weight: normal;
      padding-top: 102px;
    }

    &--passion {
      font-size: 48px;
      line-height: 1.04;
      color: $color-accent-text;
    }
  }
}

.hero__btns {
  display: flex;
}

.hero__button,
.hero__button-white {
  margin: 0;
  font-weight: normal;
  font-size: 8px;
  line-height: 0.8;
  text-decoration: none;
  padding-top: 8px;
  padding-bottom: 8px;
  background-color: $color-bcg-btn;
  border-radius: 22px;
  border: none;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 14px;
  padding-right: 14px;
  color: $color-white-text;
  &:hover,
  &:focus {
    background-color: $color-bcg-btn-hover;
  }
}
.hero__button-white {
  margin-left: 8px;
  background-color: $color-bcg-btn-white;
  color: $color-accent-text;
  &:hover,
  &:focus {
    background-color: $color-bcg-btn-white-hover;
  }
}

.hero__content-btn {
  position: relative;
  width: 18px;
  height: 18px;
  background: $color-bcg-review;
  border-radius: 50%;
}

.hero__block {
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 768px) {
    display: block;
    margin-top: 19px;
    height: 152px;
    background-image: url(../images/tablet/slider-pic5.png);
    background-size: 124px 126px;
    background-position: 0px -28px;
    background-repeat: no-repeat;
  }
}
.hero__text {
  font-weight: 400;
  font-size: 8px;
  line-height: 1.5;
  color: $color-white-text;
  margin-bottom: 7px;
}
.hero__block-content {
  margin-left: 120px;
  max-width: 114px;
  padding-top: 19px;
}

.hero__places {
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 768px) {
    display: block;
    min-width: 202px;
    padding-top: 94px;
    height: 399px;
    background-image: url(../images/tablet/slider-pic4.png);
    background-size: 168px 133px;
    background-position: bottom;
    background-repeat: no-repeat;
  }
}
.hero__box {
  background-color: #ffb8ca;
  @media screen and (max-width: 767px) {
    background-image: url(../images/phone/ellipse-hero-mob.png);
    background-size: 346px 344px;
    background-position: 34px 23px;
    background-repeat: no-repeat;
    //  width: 100%;
  }
  @media screen and (min-width: 768px) {
    display: flex;
    background-image: url(../images/tablet/ellipse-hero-tab.png);
    background-size: 324px 322px;
    background-position: 307px 31px;
    background-repeat: no-repeat;
  }
}

.hero__icon {
  position: absolute;
  left: auto;
  right: auto;
  transform: translate(-50%, -50%);
  fill: $color-bcg-btn;
}
.hero__number {
  font-family: 'Titan One';
  font-weight: normal;
  font-size: 28px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: $color-accent-text;
  text-align: right;
}
.hero__places-content {
  display: flex;
  flex-direction: column;
  width: 51px;
  margin-left: auto;
}
.hero__number-text {
  font-weight: normal;
  font-size: 8px;
  line-height: 10px;
  margin-left: auto;
  color: $color-white-text;
}
