img {     
    width: inherit;
}

.cow {
    padding: 125px 0 0 0;  
    margin: 0;   

    color: $color-grey-text;
    font-family: 'DM Sans';
    font-size: 14px;
    line-height: 1.76;
    letter-spacing: 0.04em; 

    overflow-x:hidden;    
}

.cow--background {
    background-color: $color-bcg-light-pink;
}

.cow__title {   
    padding: 0 20px 26px 20px;  
    
    text-align: center;    

    color: $color-accent-text;
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;  
    letter-spacing: 0.04em;      
    text-transform: uppercase;      
  
    @media screen and (min-width: 772px) {
        padding-bottom: 50px;
        font-size: 48px;           
    }
  
    @media screen and (min-width: 1280px) {    
        padding-bottom: 55px;
        font-size: 58px;
        line-height: 1.14;
        letter-spacing: 0.06em;        
    }
}

.cow-conteiner { 
    margin: 0 auto;          

    @media screen and (max-width: 771px) {
        background-image: url(../images/milk-320.png);
        background-size: contain;
        background-position: 0 91%;
        background-repeat: no-repeat; 
    }
    
    @media screen and (min-width: 320px) {
        width: 320px;          
    }

    @media screen and (min-width: 772px) {
        width: 772px;  
    }

    @media screen and (min-width: 1280px) {
        width: 1280px;  

        background-image: url(../images/milk.png);        
        background-size: contain;
        background-position: bottom center;
        background-repeat: no-repeat;
    }
}

.cow-conteiner__title {    
    padding-bottom: 15px;
    
    color: $color-pink-text;
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;    

    text-transform: uppercase; 
    text-align: center;    

    @media screen and (min-width: 772px) {
        padding-bottom: 16px;
        font-size: 26px;   
        line-height: 1.15;                
    }
  
    @media screen and (min-width: 1280px) {        
        padding-bottom: 15px;
        font-size: 30px;
        line-height: 1.13;      
    }
}

.about {
    display: block;    
  
    @media screen and (min-width: 772px) {
        display: flex;   
        flex-wrap: nowrap;      
    }  
}

.cow-conteiner__img {      

    @media screen and (min-width: 320px) {          
        // margin: 0 20px 46px 20px;           
        width: 280px;  
        height: 279px;
    }  

    @media screen and (min-width: 772px) {
        margin: 0 17px 0 39px;
        width: 340px;    
        height: 339px;           
    }
  
    @media screen and (min-width: 1280px) {
        margin: 0 74px 0 112px;
        width: 516px;  
        height: 516px;         
    }
}

.cow-conteiner__description {
    display: block;       
    padding: 46px 50px 40px 50px;
   
    font-family: 'DM Sans';       
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    letter-spacing: 0.00em;

    color: $color-black-text;      

    @media screen and (min-width: 772px) {
        padding: 50px 36px 36px 0;

        font-size: 14px;
        line-height: 1.76;
    }
  
    @media screen and (min-width: 1280px) {
        padding: 51px 117px 35px 0;

        font-size: 16px;        
        letter-spacing: 0.04em;
    }
}

.cow-conteiner__information {  
    display: block;     
    padding: 0 50px 0 50px;   
        
    @media screen and (min-width: 772px) {
        padding: 0 36px 45px 0;
    }
  
    @media screen and (min-width: 1280px) {
        padding: 0 117px 0 0;
    }
}

.cow-conteiner__text {    
    display: block;
    padding-bottom: 17px;

    font-family: DM Sans;       
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;
    letter-spacing: 0;        
          
    @media screen and (min-width: 772px) {         
        font-size: 14px;
        line-height: 1.76; 
        letter-spacing: 0.04em;
    }

    @media screen and (min-width: 1280px) {
        padding-bottom: 45px;     

        line-height: 1.94;            
    }
}

.break {
    display: block;

    @media screen and (min-width: 772px) {
        display: inline;
    }
}

.cow-button {      
    @extend %link;

    display: flex;
    padding: 0;
    margin: 0 auto 39px auto;
    width: 176px;
    height: 44px;

    border: 1px solid transparent;        
    border-radius: 22px;
    background-color: $color-bcg-btn-white;

    color: $color-accent-text;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;        
    
    text-align: center;  
    align-items: center;   
    justify-content: center;

    transition-property: background-color; 
    
    @media screen and (min-width: 320px) {
        margin: 0 94px 39px 50px;    
    }

    @media screen and (min-width: 772px) {
        margin: 0 200px 66px 0;    
    }
    
    @media screen and (min-width: 1280px) {
        margin: 0 404px 146px 0;      
    }
}

.cow-button:hover,
.cow-button:focus {
    background-color: $color-bcg-btn-white-hover; 
} 

.cow-button__icon {
    padding: 0;
    margin-left: 13px;
    width: 6px;
    height: 10px;
    color: $color-bcg-btn;
    fill: transparent;                       
}